import Graph from "graphology";
import React from "react";

export interface RouterNode {
  path: string;
  component: React.ReactNode;
}

export class GraphRouter<T extends RouterNode = RouterNode> {
  public graph: T[] = [];
  public paths: RouterNode["path"][] = [];

  // Private
  #graph = new Graph();

  generateGraph = () => {
    const currentGraph = this.#graph;

    this.graph.forEach(({ path }) => {
      currentGraph.addNode(path);
    });
    console.log("aaa");

    this.paths = currentGraph.nodes();
  };

  constructor() {
    this.generateGraph();
  }
}
